html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif;
  background-color: #f0f0f0;
}

img {
  max-width: 100%;
  display: block;
}

p {
  font-size: 2rem;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=container],
.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.img {
  width: 100%;
}

.section {
  padding: 8rem 0;
  overflow: hidden;
}

[class$=heading] {
  margin: 2rem 0;
}

.header {
  padding-top: 2rem;
}
.header__grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__grid {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo-img {
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo-img {
    margin: 0;
  }
}

.networks {
  margin: 5rem 0;
  display: flex;
  gap: 1rem;
}
@media (min-width: 768px) {
  .networks {
    margin: 0;
  }
}
.networks__aria {
  display: block;
  overflow: hidden;
  border: 0;
  padding: 0;
  margin: -0.1rem;
  width: 0.1rem;
  height: 0.1rem;
  clip: rect(0, 0, 0, 0);
}
.networks__link {
  border-bottom: 0.3rem solid transparent;
  transition: all 0.2s ease;
}
.networks__link::before {
  content: "";
  padding: 2rem;
  display: inline-block;
}
.networks__link[href*="facebook.com"]::before {
  background-image: url("../img/svg/icono_facebook.svg");
}
.networks__link[href*="twitter.com"]::before {
  background-image: url("../img/svg/icono_twitter.svg");
}
.networks__link[href*="instagram.com"]::before {
  background-image: url("../img/svg/icono_instagram.svg");
}
.networks__link[href*="pinterest.com"]::before {
  background-image: url("../img/svg/icono_pinterest.svg");
}
.networks__link[href*="youtube.com"]::before {
  background-image: url("../img/svg/icono_youtube.svg");
}
.networks__link[href*="tiktok.com"]::before {
  background-image: url("../img/svg/icono_tiktok.svg");
}
.networks__link:hover {
  border-bottom: 0.3rem solid #8ed3cc;
}

.navbar {
  padding: 0.3rem 0;
  margin: 2rem 0;
  background-color: #8ed3cc;
}
.navbar__border {
  padding: 1.7rem 0;
  border-block: 0.2rem dashed #ffffff;
}
.navbar__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1.5rem;
}
@media (min-width: 768px) {
  .navbar__container {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navbar__link {
  font-size: 2.4rem;
  transition: all 0.2s ease;
  color: #ffffff;
}
.navbar__link:hover {
  color: #d6598c;
}

.products__heading {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: lowercase;
  font-family: "Italianno", sans-serif;
  color: #e082a8;
  margin-bottom: 5rem;
}
.products__heading span {
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
  color: #000000;
  display: block;
}
.products__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .products__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .products__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.product__img {
  width: 100%;
  object-fit: cover;
  transition: all 0.2s ease;
}
.product__img:hover {
  transform: scale(1.1);
}
.product__content {
  padding: 2rem;
  background-color: #ffffff;
}
.product__name {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: uppercase;
  font-size: 2.4rem;
  margin-bottom: 1rem;
  color: #e082a8;
}
.product__description {
  font-size: 1.6rem;
  line-height: 1.8;
  text-transform: uppercase;
}
.product__price {
  text-align: center;
  font-size: 2.8rem;
  color: #979797;
}

.slider {
  position: relative;
}
.slider__image-container {
  flex-shrink: 0;
  height: 100%;
  width: 100%;
  position: relative;
  transition-property: transform;
}
.slider__img {
  width: 100%;
}

.welcome {
  text-align: center;
}
.welcome__heading {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: lowercase;
  font-family: "Italianno", sans-serif;
  color: #e082a8;
}
.welcome__heading span {
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
  color: #000000;
  display: block;
}
.welcome__text {
  font-size: 2.6rem;
  line-height: 1.8;
  max-width: 50rem;
  margin: 4rem auto 0 auto;
  color: #979797;
}

@media (min-width: 768px) {
  .cards__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.card {
  max-width: 40rem;
  margin: 0 auto 2rem auto;
}
.card:last-of-type {
  margin-bottom: 0;
}
.card__img {
  width: 100%;
  transition: all 0.2s ease;
  border: 0.5rem solid #ffffff;
}
.card__img:hover {
  transform: scale(1.05);
}
.card__content {
  max-width: 90%;
  margin: 0 auto;
  transform: translateY(-5rem);
  background-color: #ffffff;
}
.card__heading {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: lowercase;
  font-family: "Italianno", sans-serif;
  color: #e082a8;
  font-size: 3.4rem;
  padding: 2rem;
}
.card__heading span {
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
  color: #000000;
  display: block;
}
.card__link {
  font-size: 2.2rem;
  padding: 2rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.2s ease;
  cursor: pointer;
  color: #ffffff;
  background-color: #e082a8;
  display: block;
}
.card__link:hover {
  background-color: #d6598c;
}
.card__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .card__link {
    width: auto;
  }
}
@media (min-width: 768px) {
  .card {
    max-width: unset;
    margin: 0;
  }
}

.notavif.notwebp .agenda {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url("../img/page/bg_horarios.jpg");
}
@media (min-width: 768px) {
  .notavif.notwebp .agenda {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url("../img/page/bg_horarios.jpg");
  }
}

.webp .agenda {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url("../img/page/bg_horarios.webp");
}
@media (min-width: 768px) {
  .webp .agenda {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url("../img/page/bg_horarios.webp");
  }
}

.avif .agenda {
  background-image: linear-gradient(to bottom, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url("../img/page/bg_horarios.avif");
}
@media (min-width: 768px) {
  .avif .agenda {
    background-image: linear-gradient(to right, #8ed3cc 0%, #8ed3cc 50%, transparent 50%), url("../img/page/bg_horarios.avif");
  }
}

.agenda {
  background-repeat: no-repeat;
  background-position: center, bottom center;
  color: #ffffff;
}
.agenda__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .agenda__grid {
    grid-template-rows: unset;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.agenda__content {
  padding: 5rem 0;
}
.agenda__heading {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: uppercase;
}
.agenda__text {
  font-size: 2.2rem;
  line-height: 1.8;
  text-align: center;
}
@media (min-width: 768px) {
  .agenda {
    background-position: center, top right;
  }
}

.table {
  width: 100%;
  text-align: center;
  border-collapse: collapse;
  border: 0.5rem solid #e082a8;
}
.table__thead {
  background-color: #e082a8;
}
.table__th {
  padding: 2rem;
}
.table__tr {
  transition: all 0.2s ease;
}
.table__tr:not(:last-of-type):hover {
  background-color: #69c5bb;
}
.table__td {
  padding: 1rem;
  font-size: 2.2rem;
}

.notavif.notwebp .appointment {
  background-image: url("../img/page/bg_citas.jpg");
}

.webp .appointment {
  background-image: url("../img/page/bg_citas.webp");
}

.avif .appointment {
  background-image: url("../img/page/bg_citas.avif");
}

.appointment {
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  text-align: center;
}
.appointment > * {
  color: #ffffff;
}
.appointment__heading {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: uppercase;
  font-size: 6rem;
}
.appointment__text {
  max-width: 60rem;
  margin: 0 auto;
  text-align: center;
  line-height: 2;
}
.appointment__link {
  font-size: 2.2rem;
  padding: 2rem;
  border: none;
  font-weight: 700;
  display: inline-block;
  margin-top: 2rem;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.2s ease;
  cursor: pointer;
  color: #ffffff;
  background-color: #8ed3cc;
  padding: 2rem 10rem;
}
.appointment__link:hover {
  background-color: #69c5bb;
}
.appointment__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .appointment__link {
    width: auto;
  }
}

.footer {
  padding-top: 5rem;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
.footer__widget {
  text-align: center;
}
.footer__heading {
  text-align: center;
  line-height: 0.8;
  font-weight: 400;
  font-size: 4rem;
  text-transform: uppercase;
  font-size: 3.4rem;
}
.footer__text {
  line-height: 1.8;
}
.footer__networks {
  justify-content: center;
}
.footer__copyright {
  text-align: center;
  max-width: 120rem;
  margin: 2rem auto 0 auto;
  padding: 2rem 0;
  border-top: 0.2rem solid #dfdfdf;
  color: #e082a8;
}

.mask {
  overflow: hidden;
  width: 100%;
}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-slide img {
  display: block;
  width: 100%;
}